.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.map-container {
  position: relative;
  width: 100%;
  height: 100vh;
}

.map-view-button-group {
  position: absolute;
  top: 700px;
  right: 10px;
  display: flex;
  flex-direction: column;
}

.map-view-start-stop-button {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%); /* 这个用于使元素居中对齐 */
}


.tracking-button {
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 20px;
}

.clear-button {
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 5px 10px;
  background-color: #c62828; /* red */
  color: #ffffff; /* white */
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.clear-button:hover {
  background-color: #b71c1c; /* darker red */
}

.clear-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.Navigation {
  width: 100%;
  height: 100vh;
}

.visibility-button {
  padding: 8px 16px;
  background-color: #f1f1f1;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 10px;
}

/* CSS for Add Point button */
.add-point-button {
  padding: 8px 16px;
  background-color: #f1f1f1;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

